.product{
    display: flex;
    flex-direction: column;
    background-color:white;
    align-items: center;
    justify-content: space-between;
    margin: 10px;
    padding: 20px;
    width: 100%;
    max-height: 400px;
    min-width: 100px;
    z-index: 1;
    border-radius: 10px;
    overflow: hidden;
    

}
.product:hover{
    border: 1px solid lightblue;
    cursor: pointer;
}
.product_image{
    
    width: 150px;
    max-height: 200px;
    object-fit: contain;
    margin-bottom: 15px;

}
.product:hover img{
    
    scale: 1.2;
    transition: all ease 1s;
}
.product > button{
    background: #f0ca4b;
    border:1px solid;
    margin-top: 10px;
    border-color: #a88748 #9c7e31 #846a29;
    color: #111;
}
.product > button:hover{
    color: white;
}
.product_info{
    height: 100px;
    margin-bottom: 15px;
}
.product_price{
    margin-top: 5px;
}
.product_rating{
    display: flex;
}

